@import "./varaibles";

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border-radius: 0px;
    font-family: 'Geometria', sans-serif;
    color: $text-color; }

img {
    max-width: 100%; }

a {
    text-decoration: none; }

body, html {
    overflow-x: hidden;
    &.no-scroll {
        overflow: hidden; } }

section {
    position: relative; }

body {
    background-color: $body-bg;
    min-height: 100vh;
    display: flex;
    flex-direction: column; }
