.requisites {
  padding: 65px 0px 70px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../img/requisites-bg.jpg);
}

.requisites__list {
  margin-top: 70px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  column-gap: 130px;
  row-gap: 35px;
  list-style-type: none;
}

.requisites__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 20px;
  line-height: 170%;
  color: #ffffff;
}

.requisites__item span {
  color: inherit;
  line-height: inherit;
  font-size: inherit;
}
