.hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../img/hero-bg.jpg);
  height: 820px;
}

.hero__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 44px;
  height: 820px;
}

.hero__title {
  font-size: 90px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  margin: auto 0px 70px;
}

.hero__contacts {
  margin-bottom: 150px;
}

.hero__tel, .hero__mail {
  font-weight: 500;
  text-align: center;
  display: inline-block;
  width: 100%;
}

.hero__tel {
  font-size: 44px;
  color: #72A1B3;
  margin-bottom: 15px;
}

.hero__mail {
  font-size: 26px;
  color: #F84001;
}

.hero__scroll-link {
  display: inline-block;
  height: 50px;
  width: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url(../img/mouse-down.png);
}

.hero .container {
  height: 820px;
}
