@import "./varaibles";

.catalog {
    padding: 90px 0px 100px;
    &__list {
        margin: 70px 0px 200px;
        list-style-type: none;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(390px, 1fr));
        column-gap: 40px;
        row-gap: 70px; }
    &__item {
        display: flex;
        align-items: center;
        justify-content: flex-start; }
    &__icon + &__name {
        margin-left: 50px; }
    &__icon {
        width: 100px;
        height: auto;
        object-fit: contain; }
    &__name {
        font-size: 26px;
        line-height: 38px; } }
