@import "./varaibles";

.hero {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../img/hero-bg.jpg);
    $height: 820px;
    height: $height;
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-bottom: 44px;
        height: $height; }
    &__title {
        font-size: 90px;
        font-weight: 700;
        color: #ffffff;
        text-align: center;
        margin: auto 0px 70px; }
    &__contacts {
        margin-bottom: 150px; }
    &__tel, &__mail {
        font-weight: 500;
        text-align: center;
        display: inline-block;
        width: 100%; }
    &__tel {
        font-size: 44px;
        color: #72A1B3;
        margin-bottom: 15px; }
    &__mail {
        font-size: 26px;
        color: #F84001; }
    &__scroll-link {
        display: inline-block;
        height: 50px;
        width: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-image: url(../img/mouse-down.png); }
    .container {
        height: $height; } }
